import React, { useState, useEffect } from "react";
import {
  Text,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableContainer,
  Stack,
  Select,
  IconButton,
  Editable,
  EditableInput,
  EditablePreview,
  Tooltip,
  Card,
  CardBody,
  Avatar,
  useBreakpointValue,
  Checkbox,
  Button,
  Link,
  PopoverContent,
  Popover,
  PopoverTrigger,
  PopoverHeader,
  PopoverArrow,
  PopoverCloseButton,
  PopoverBody,
  Code,
  PopoverFooter,
  ButtonGroup,
  Spinner,
} from "@chakra-ui/react";
import {
  DeleteIcon,
  CopyIcon,
  DragHandleIcon,
  AtSignIcon,
  ChevronDownIcon,
} from "@chakra-ui/icons";
import {
  useDeleteApplicantMutation,
  useUpdateApplicantMutation,
  useUpdateAppointmentMutation,
  useDeleteAppointmentMutation,
  useGetAllUsersQuery,
  useDeassignUserMutation,
  useAssignUserMutation,
} from "../../store/api-slice";
import {
  getAppointmentCode,
  getLoginCode,
  isInputDisabled,
} from "../../utils/functions";

function Appointment({ appointment, attributes, permission, isFetching }) {
  const { applicants } = appointment;

  return (
    <Card variant="outline" size="sm">
      <CardBody>
        <AppointmentDetails
          permission={permission}
          appointment={appointment}
          attributes={attributes}
          isFetching={isFetching}
        />
        <Applicants
          permission={permission}
          applicants={applicants}
          isFetching={isFetching}
        />
      </CardBody>
    </Card>
  );
}

function Applicants({ applicants, permission, isFetching }) {
  const [deleteApplicant] = useDeleteApplicantMutation();
  const [updateApplicant] = useUpdateApplicantMutation();
  const [editedValues, setEditedValues] = useState({});

  // controllers
  function handleDeleteClick(id) {
    deleteApplicant(id);
  }

  async function handleEditClick(id) {
    await updateApplicant({
      id,
      data: editedValues[id],
    });
    setEditedValues((prevState) => {
      const newState = { ...prevState };
      delete newState[id];
      return newState;
    });
  }

  function handleCancelClick(id) {
    setEditedValues((prevState) => {
      const newState = { ...prevState };
      delete newState[id];
      return newState;
    });
  }

  function handleChange(id, property, value) {
    setEditedValues((prevState) => ({
      ...prevState,
      [id]: { ...prevState[id], [property]: value },
    }));
  }

  function handleEdit(id, property) {
    setEditedValues((prevState) => ({
      ...prevState,
      [id]: {
        ...prevState[id],
        [property]: applicants.find((applicant) => applicant._id === id)[
          property
        ],
      },
    }));
  }

  return (
    <TableContainer>
      <Table size="sm">
        <Thead>
          <Tr>
            <Th textAlign="center">Image</Th>
            <Th textAlign="center">First Name</Th>
            <Th textAlign="center">Last Name</Th>
            <Th textAlign="center">Passport</Th>
            <Th textAlign="center">Date Of Birth</Th>
            {!isInputDisabled(permission, "delete") && (
              <Th textAlign="center">Actions</Th>
            )}
          </Tr>
        </Thead>
        <Tbody>
          {applicants.length > 0 ? (
            applicants.map(
              ({
                _id = null,
                firstName,
                lastName,
                passportNumber,
                dateOfBirth,
                image,
              }) => (
                <Tr key={_id}>
                  <Td textAlign="center">
                    <Avatar
                      style={{ cursor: "pointer" }}
                      src={image}
                      as={Link}
                      href={image}
                      target="_blank"
                    />
                  </Td>

                  <Td textAlign="center">
                    <InputArea
                      permission={permission}
                      fieldName="firstName"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      value={editedValues[_id]?.firstName ?? firstName}
                      isFetching={isFetching}
                      id={_id}
                    />
                  </Td>

                  <Td textAlign="center">
                    <InputArea
                      permission={permission}
                      fieldName="lastName"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      value={editedValues[_id]?.lastName ?? lastName}
                      isFetching={isFetching}
                      id={_id}
                    />
                  </Td>

                  <Td textAlign="center">
                    <InputArea
                      permission={permission}
                      fieldName="passportNumber"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={_id}
                      value={
                        editedValues[_id]?.passportNumber ?? passportNumber
                      }
                    />
                  </Td>

                  <Td textAlign="center">
                    <InputArea
                      permission={permission}
                      fieldName="dateOfBirth"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={_id}
                      value={
                        editedValues[_id]?.dateOfBirth?.split("T")[0] ??
                        dateOfBirth.split("T")[0]
                      }
                    />
                  </Td>

                  {!isInputDisabled(permission, "delete") && (
                    <Td textAlign="center">
                      <Stack direction="row" align="center" justify="center">
                        <IconButton
                          aria-label="Delete"
                          size="sm"
                          icon={<DeleteIcon />}
                          onClick={() => handleDeleteClick(_id)}
                        />
                      </Stack>
                    </Td>
                  )}
                </Tr>
              )
            )
          ) : (
            <Tr>
              <Td textAlign="center">NULL</Td>
              <Td textAlign="center">NULL</Td>
              <Td textAlign="center">NULL</Td>
              <Td textAlign="center">NULL</Td>
              <Td textAlign="center">NULL</Td>
              <Td textAlign="center">NULL</Td>
            </Tr>
          )}
        </Tbody>
      </Table>
    </TableContainer>
  );
}

function AppointmentDetails({
  permission,
  appointment,
  attributes,
  isFetching,
}) {
  const { ownerEmuns, visaEmuns, statusEmuns } = attributes;
  const {
    _id: appointmentId,
    numberOfApplicants,
    expectedTravelDate,
    email,
    phone,
    note,
    status,
    owner,
    visa,
  } = appointment;

  const { data: usersRes, isLoading: isUsersLoading } = useGetAllUsersQuery();
  const [deleteAppointment] = useDeleteAppointmentMutation();
  const [deassignUser] = useDeassignUserMutation();
  const [assignUser] = useAssignUserMutation();
  const [updateAppointment] = useUpdateAppointmentMutation();

  const [usersState, setUsersState] = useState({});
  const [editedValues, setEditedValues] = useState({});
  const layout = useBreakpointValue({ base: "default", md: "fixed" });

  useEffect(() => {
    if (!isUsersLoading) {
      const states = {};
      usersRes?.user.map(
        (user) => (states[user._id] = isUserAssigned(user._id, appointment))
      );

      setUsersState(states);
    }
  }, [appointment, isUsersLoading, usersRes]);

  function isUserAssigned(id, appointment) {
    return appointment.assignedUsers.includes(id);
  }

  async function handleUserChange(e, { userId, appointmentId }) {
    const isChecked = e.target.checked;
    if (isChecked) {
      await assignUser({ userId, appointmentId });
    } else {
      await deassignUser({ userId, appointmentId });
    }
    setUsersState((prevState) => ({ ...prevState, [userId]: isChecked }));
  }

  function handleEditClick(id) {
    updateAppointment({
      id,
      data: editedValues[id],
    });
    setEditedValues((prevState) => {
      const newState = { ...prevState };
      delete newState[id];
      return newState;
    });
  }

  function handleEdit(id, property) {
    setEditedValues((prevState) => ({
      ...prevState,
      [id]: {
        ...prevState[id],
        [property]: appointment[property],
      },
    }));
  }

  function handleCancelClick(id) {
    setEditedValues((prevState) => {
      const newState = { ...prevState };
      delete newState[id];
      return newState;
    });
  }

  function handleChange(id, property, value) {
    setEditedValues((prevState) => ({
      ...prevState,
      [id]: { ...prevState[id], [property]: value },
    }));
  }

  function handleSelectChange(e, fieldName) {
    updateAppointment({
      id: appointmentId,
      data: { [fieldName]: e.target.value },
    });
  }

  return (
    <Stack direction="row">
      <Card mb={5} size="sm" width="full">
        <CardBody>
          <TableContainer>
            <Table size="sm" layout={layout} width="full" variant="simple">
              <Tbody>
                <Tr>
                  <Td>
                    <Text as="b">Expected Travel Date:</Text>
                  </Td>
                  <Td textAlign="center" overflow="clip">
                    <InputArea
                      permission={permission}
                      fieldName="expectedTravelDate"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={appointmentId}
                      value={
                        editedValues[appointmentId]?.expectedTravelDate?.split(
                          "T"
                        )[0] ?? expectedTravelDate.split("T")[0]
                      }
                    />
                  </Td>
                  <Td>
                    <Text as="b">Status:</Text>
                  </Td>
                  <Td>
                    <SelectArea
                      permission={permission}
                      fieldName="status"
                      value={editedValues[appointmentId]?.status ?? status}
                      handleChange={handleSelectChange}
                      enums={statusEmuns}
                      isFetching={isFetching}
                    />
                  </Td>
                </Tr>
                <Tr>
                  <Td>
                    <Text as="b">Email:</Text>
                  </Td>
                  <Td textAlign="center" overflow="clip">
                    <InputArea
                      permission={permission}
                      fieldName="email"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={appointmentId}
                      value={editedValues[appointmentId]?.email ?? email}
                    />
                  </Td>

                  <Td>
                    <Text as="b">Visa:</Text>
                  </Td>
                  <Td textAlign="center">
                    <SelectArea
                      permission={permission}
                      fieldName="visa"
                      value={editedValues[appointmentId]?.visa ?? visa}
                      handleChange={handleSelectChange}
                      enums={visaEmuns}
                      isFetching={isFetching}
                    />
                  </Td>
                </Tr>
                <Tr>
                  <Td>
                    <Text as="b">Mobile Number:</Text>
                  </Td>
                  <Td textAlign="center">
                    <InputArea
                      permission={permission}
                      fieldName="phone"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={appointmentId}
                      value={editedValues[appointmentId]?.phone ?? phone}
                    />
                  </Td>
                  <Td>
                    <Text as="b">Owner:</Text>
                  </Td>
                  <Td textAlign="center">
                    <SelectArea
                      permission={permission}
                      fieldName="owner"
                      value={editedValues[appointmentId]?.owner ?? owner}
                      handleChange={handleSelectChange}
                      enums={ownerEmuns}
                      isFetching={isFetching}
                    />
                  </Td>
                </Tr>
                <Tr>
                  <Td>
                    <Text as="b">Note:</Text>
                  </Td>
                  <Td textAlign="center" overflow="clip">
                    <InputArea
                      permission={permission}
                      fieldName="note"
                      handleEditClick={handleEditClick}
                      handleCancelClick={handleCancelClick}
                      handleEdit={handleEdit}
                      handleChange={handleChange}
                      isFetching={isFetching}
                      id={appointmentId}
                      value={editedValues[appointmentId]?.note ?? (note || "-")}
                    />
                  </Td>

                  <Td>
                    <Text as="b">No. Applicants:</Text>
                  </Td>
                  <Td textAlign="center">
                    <Text>{numberOfApplicants?.toString()}</Text>
                  </Td>
                </Tr>
              </Tbody>
            </Table>
          </TableContainer>
        </CardBody>
      </Card>
      <Stack>
        {!isInputDisabled(permission, "delete") && (
          <Tooltip label="Delete" placement="right">
            <IconButton
              aria-label="Delete"
              size="sm"
              icon={<DeleteIcon />}
              variant="outline"
              onClick={() => deleteAppointment(appointmentId)}
            />
          </Tooltip>
        )}
        {!isInputDisabled(permission, "assign") && (
          <Popover placement="left" isLazy={true}>
            <PopoverTrigger>
              <IconButton
                variant="outline"
                aria-label="Assign"
                size="sm"
                icon={<DragHandleIcon />}
              />
            </PopoverTrigger>
            <PopoverContent>
              <PopoverHeader fontWeight="semibold">Users</PopoverHeader>
              <PopoverArrow />
              <PopoverCloseButton />
              <PopoverBody>
                {!isUsersLoading &&
                  usersRes?.user.map(({ email, _id }) => (
                    <Checkbox
                      key={_id}
                      onChange={(e) =>
                        handleUserChange(e, { userId: _id, appointmentId })
                      }
                      isChecked={usersState[_id]}>
                      {email}
                    </Checkbox>
                  ))}
              </PopoverBody>
            </PopoverContent>
          </Popover>
        )}
        {!isInputDisabled(permission, "copy_code") && (
          <Popover placement="left" isLazy={true}>
            <PopoverTrigger>
              <IconButton
                variant="outline"
                aria-label="Copy code"
                size="sm"
                icon={<CopyIcon />}
              />
            </PopoverTrigger>
            <PopoverContent width={600} textAlign="left">
              <PopoverHeader fontWeight="semibold">Code</PopoverHeader>
              <PopoverArrow />
              <PopoverCloseButton />
              <PopoverBody>
                <Code
                  whiteSpace="pre-wrap"
                  sx={{ wordBreak: "break-all" }}
                  maxH={400}
                  overflowY="scroll">
                  {getAppointmentCode(appointment)}
                </Code>
              </PopoverBody>
              <PopoverFooter display="flex" justifyContent="flex-end">
                <ButtonGroup size="sm">
                  <Button
                    colorScheme="blue"
                    onClick={() =>
                      navigator.clipboard.writeText(
                        getAppointmentCode(appointment)
                      )
                    }>
                    Copy
                  </Button>
                </ButtonGroup>
              </PopoverFooter>
            </PopoverContent>
          </Popover>
        )}
        {!isInputDisabled(permission, "copy_email") && (
          <Popover placement="left" isLazy={true}>
            <PopoverTrigger>
              <IconButton
                variant="outline"
                aria-label="Copy code"
                size="sm"
                icon={<AtSignIcon />}
              />
            </PopoverTrigger>
            <PopoverContent width={600} textAlign="left">
              <PopoverHeader fontWeight="semibold">Code</PopoverHeader>
              <PopoverArrow />
              <PopoverCloseButton />
              <PopoverBody>
                <Code
                  width="full"
                  whiteSpace="pre-wrap"
                  sx={{ wordBreak: "break-all" }}>
                  {getLoginCode(appointment.email)}
                </Code>
              </PopoverBody>
              <PopoverFooter display="flex" justifyContent="flex-end">
                <ButtonGroup size="sm">
                  <Button
                    colorScheme="blue"
                    onClick={() =>
                      navigator.clipboard.writeText(
                        getLoginCode(appointment.email)
                      )
                    }>
                    Copy
                  </Button>
                </ButtonGroup>
              </PopoverFooter>
            </PopoverContent>
          </Popover>
        )}
      </Stack>
    </Stack>
  );
}

function InputArea({
  permission,
  fieldName,
  value,
  id,
  handleEditClick,
  handleCancelClick,
  handleEdit,
  handleChange,
  isFetching,
}) {
  return isInputDisabled(permission, fieldName) ? (
    <Text textAlign="center">{value}</Text>
  ) : (
    <Editable
      onSubmit={() => handleEditClick(id)}
      onCancel={() => handleCancelClick(id)}
      onEdit={() => handleEdit(id, fieldName)}
      isDisabled={isInputDisabled(permission, fieldName)}
      submitOnBlur={false}
      value={value}>
      {isFetching ? (
        <Spinner />
      ) : (
        <Tooltip label="Click to edit">
          <EditablePreview />
        </Tooltip>
      )}
      <EditableInput
        onChange={(e) => handleChange(id, fieldName, e.target.value)}
      />
    </Editable>
  );
}

function SelectArea({
  permission,
  fieldName,
  value,
  handleChange,
  enums,
  isFetching,
}) {
  return isInputDisabled(permission, fieldName) ? (
    <Text textAlign="center">{value}</Text>
  ) : (
    <Select
      textAlign="center"
      size="sm"
      value={value}
      icon={isFetching ? <Spinner /> : <ChevronDownIcon />}
      onChange={(e) => handleChange(e, fieldName)}>
      {enums.map((label, index) => (
        <option key={index} value={label}>
          {label}
        </option>
      ))}
    </Select>
  );
}

export default Appointment;
